import "../css/table.css";
import { useEffect, useState } from "react";

// Props.data should be an object array

function Table(props) {
  // const [data,setData] = React.useState(props.data)
  const [page, setpage] = useState(1);
  const [pagelimit, setpagelimit] = useState(10);
  const lastpage = Math.ceil(props.data.length / pagelimit);
  useEffect(() => {
    // console.log(props.data);
  }, []);

  const generateHead = () => {
    const keys = Object.keys(props.data[0]);
    return (
      <tr>
        {keys.map((e, i) => (
          <th key={i}>{e}</th>
        ))}
      </tr>
    );
  };
  const generateRow = (data, index) => {
    // const data = props.data[0];
    // console.log(data);
    const keys = Object.keys(data);
    return (
      <tr key={index}>
        {keys.map((e, i) => (
          <td key={i}>{data[e]}</td>
        ))}
      </tr>
    );
  };
  const generateBody = (data) => {
    // const data = props.data;
    return (
      <tbody>
        {data.map((e, i) => generateRow(e, i))}
        {/* {generateRow(props.data[0])}
        {generateRow(props.data[1])}
        {generateRow(props.data[2])}
        {generateRow(props.data[3])} */}
      </tbody>
    );
  };

  const displayBody = () => {
    const start = (page - 1) * pagelimit;
    const end = start + pagelimit;
    const filteredData = props.data.slice(start, end);
    console.log(filteredData);
    return generateBody(filteredData);
  };

  const generateButtons = () => {
    const buttons = [];
    const buttonoffset = 3;
    const start = page - buttonoffset < 1 ? 1 : page - buttonoffset;
    const end = page + buttonoffset > lastpage ? lastpage : page + buttonoffset;

    for (let i = start; i <= end; i++) {
      buttons.push(i);
    }
    return buttons.map((e, i) => (
      <button
        className={page === e ? "currentpage" : ""}
        key={i}
        onClick={() => setpage(e)}
      >
        {e}
      </button>
    ));
  };

  useEffect(() => {
    // called whenever the pagelimit changes(with dependancy)
    console.log(pagelimit);
  }, [pagelimit]);

  //   function displayBody() {
  //     const start = (page - 1) * 10;
  //     const end = start + 10;
  //     const filteredData = props.data.slice(start, end);
  //     console.log(filteredData);
  //     return generateBody(filteredData);
  //   };

  // const displayBody= function() {
  //     const start = (page - 1) * 10;
  //     const end = start + 10;
  //     const filteredData = props.data.slice(start, end);
  //     console.log(filteredData);
  //     return generateBody(filteredData);
  //   };

  //   displayBody();
  return (
    <div className="table">
      <div className="buttons">
        <button onClick={() => setpage(1)}>&lt;&lt;</button>
        <button disabled={page === 1} onClick={() => setpage(page - 1)}>
          &lt;
        </button>
        {generateButtons()}
        <button onClick={() => setpage(page + 1)} disabled={page === lastpage}>
          &gt;
        </button>
        <button onClick={() => setpage(lastpage)}>&gt;&gt;</button>
        {/* <button onClick={() => setpage(1)}>1</button>
        <button onClick={() => setpage(2)}>2</button>
        <button onClick={() => setpage(3)}>3</button>
        <button onClick={() => setpage(4)}>4</button>
        <button onClick={() => setpage(5)}>5</button> */}
        <select
          value={pagelimit}
          onChange={(event) => {
            console.log(event);
            console.log(event.target);
            console.log(event.target.value);
            console.log(parseInt(event.target.value));
            setpagelimit(parseInt(event.target.value));
            setpage(1);
          }}
        >
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="20">20</option>
        </select>
      </div>
      <table>
        <thead>{generateHead()}</thead>
        {/* {generateBody(props.data)} */}
        {displayBody()}
      </table>
    </div>
  );
}

export default Table;