import React from "react";
import Table from "../components/table";

function Pagination() {
  const [comments, setComments] = React.useState([]);
  const getData = async () => {
    const data = await fetch(
      "https://jsonplaceholder.typicode.com/comments"
    ).then((e) => e.json());
    console.log(data);
    setComments(data);
    console.log("End of function");
  };

  // const getData = () => {
  //   fetch("https://jsonplaceholder.typicode.com/comments")
  //     .then((e) => e.json())
  //     .then((data) => {
  //       console.log(data);
  //     });
  //   console.log("End of function");
  // };

  //   getData();
  //   React.useEffect(() => {
  //     //called everytime when load and state change(without dependenacy)
  //     getData();
  //   });

  React.useEffect(() => {
     //called firsttime load(with empty dependenacy)
    getData();
  }, []);

  return (
    <div>
      <p>Pagination</p>
      {comments.length > 0 ? <Table data={comments}></Table> : "data is loading"}
      {/* <button onClick={() => getData()}>View</button> */}
    </div>
  );
}

export default Pagination;