import "../css/login.css";
import { useForm } from "react-hook-form";

function Login() {
  
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const submit = (data) => {
    console.log(data);
  };

  return (
    <form onSubmit={handleSubmit(submit)}>
      <div className="login">
        <h3>
          <i class="fa-solid fa-book-atlas"></i> Login
        </h3>
        <label htmlFor="">
          <i class="fa-solid fa-circle-user"></i> Username:
        </label>
        <br />
        <input
          type="text"
          {...register("Username", {
            required: {
              value: true,
              message: "Username is Required",
            },
          })}
        />
        <br />
        {errors.Username && (
          <>
            <small style={{ color: "red" }}>{errors.Username.message}</small>
            <br />
          </>
        )}
        <label htmlFor="" id="pass">
          <i class="fa-solid fa-key"></i> Password
        </label>
        <br />
        <input type="text" {...register("Password", {
            required: {
              value: true,
              message: "Password is Required",
            },
            pattern: {
              value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
              message:
                "Password is Invalid",
            },
          })}
        />
        <br />
        {errors.Password && (
          <>
            <small style={{ color: "red" }}>{errors.Password.message}</small>
            <br />
          </>
        )}
        <br />
        <button>
          {" "}
          <i class="fa-solid fa-circle-play"></i> Login
        </button>
      </div>
    </form>
  );
}

export default Login;
