import Card from "../components/card";
import { useNavigate } from "react-router-dom";

function EmployeeDetails() {
  let employee = {
    Name: "Aurora",
    Age: 53,
    Email: "ante.blandit@disparturient.ca",
    "Date of joining": "10/06/2019",
    "Phone number": "1628101902299",
    Street: "493 Iaculis Rd.",
    City: "Lewiston",
    Zip: "42591-180",
    Region: "Maine",
    Country: "Sudan",
    Info: "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    EmployeeID: 101,
  };

  if (localStorage.getItem("emp")) {
    employee = JSON.parse(localStorage.getItem("emp"));
  }

  const navigate = useNavigate();

  const GenerateTableBody = () => {
    const EmployeeKeys = Object.keys(employee);
    // console.log(EmployeeKeys);
    return EmployeeKeys.map((e, i) => {
      return (
        <tr key={i}>
          <th>{e}</th>
          <td>{employee[e]}</td>
        </tr>
      );
    });
    // return (
    //   <tr>
    //     <th>Name</th>
    //     <td>Mani</td>
    //   </tr>
    // )
  };

  const back = () => {
    // alert('back');
    navigate("/employeelist");
  };

  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <Card emp={employee} btnName={() => back()} btnText="Back"></Card>
      <table>
        <thead>
          <tr>
            <th colSpan="2">Employee Details</th>
          </tr>
        </thead>
        <tbody>{GenerateTableBody()}</tbody>
      </table>
    </div>
  );
}

export default EmployeeDetails;
