import "../css/signup.css";
import { useForm } from "react-hook-form";

function Signup() {
  // const submit = (event) =>{
  //   event.preventDefault()
  //   // alert('submit')
  //   console.log(event)
  //   console.log(event.target[0].value)
  //   console.log(event.target[1].value)
  //   console.log(event.target[2].value)

  // }
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const submit = (data) => {
    console.log(data);
  };
  return (
    <form onSubmit={handleSubmit(submit)}>
      <div className="signup">
        <p>Signup</p>
        <label htmlFor="">
          <i className="fa-solid fa-circle-user"></i> Username:
        </label>
        <br />
        <input
          type="text"
          {...register("Username", {
            required: {
              value: true,
              message: "Username is Required",
            },
            minLength: {
              value: 3,
              message: "Username shoudl have 3 Characters",
            },
            maxLength: {
              value: 10,
              message: "Username should have 10 Characters",
            },
          })}
        />
        <br />
        {errors.Username && (
          <>
            <small style={{ color: "red" }}>{errors.Username.message}</small>
            <br />
          </>
        )}

        <label htmlFor="" id="pass">
          <i className="fa-solid fa-key"></i> Password
        </label>
        <br />
        <input
          type="text"
          {...register("Password", {
            required: {
              value: true,
              message: "Password is Required",
            },
            pattern: {
              value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
              message:
                "Minimum eight characters, at least one letter and one number:",
            },
          })}
        />
        <br />
        {errors.Password && (
          <>
            <small style={{ color: "red" }}>{errors.Password.message}</small>
            <br />
          </>
        )}
        <label htmlFor="" id="repass">
          <i className="fa-solid fa-key"></i> Re enter Password
        </label>
        <br />
        <input type="text" {...register("Re enter Password")} />
        <br />
        <button type="submit">
          <i className="fa-solid fa-square-caret-right"></i> Register
        </button>
        <button type="button">
          <i className="fa-solid fa-square-caret-right"></i> Cancel
        </button>
      </div>
    </form>
  );
}

export default Signup;
