function Card(props) {
  return (
    <div className="employeecard">
      <img
        src="https://static.vecteezy.com/system/resources/thumbnails/001/505/042/small/employee-icon-free-vector.jpg"
        alt=""
      />
      <p>
        <i class="fa-solid fa-user-tie"></i> Name : {props.emp.Name}
      </p>
      <p>
        <i class="fa-regular fa-id-card"></i> Employee ID :{" "}
        {props.emp.EmployeeID}
      </p>
      <button onClick={props.btnName}>
        <i class="fa-solid fa-eye"></i> {props.btnText}
      </button>
    </div>
  );
}

export default Card;
