import React from "react";
import "../css/cal.css";
import Swal from 'sweetalert2'


function Calculator() {
  //   let input = '0';
  //   const setInput = (value) => {
  //     input = value;
  //     document.getElementById('inp').value = input
  //   };

  const [input, setInput] = React.useState("");
  const clearn = () => {
    //input: 6+9
    // output : 6+
    // setInput("cleared");
    setInput(input.substring(0,input.length-1));
  };

  const result = () => {
    try {
      setInput(eval(input));
    } catch (error) {
      // input:6+9
      // output:15
      // setInput("result");
      // alert('Invalid Expression : '+input)
      Swal.fire('Invalid Expression : '+input);

    }
  };
  return (
    <div className="cal">
      <h3>Calculator</h3>
      <input type="text" id="inp" value={input}></input>
      <br />
      <button className="" onClick={() => setInput(input + "7")}>
        7
      </button>
      <button className="" onClick={() => setInput(input + "8")}>
        8
      </button>
      <button className="" onClick={() => setInput(input + "9")}>
        9
      </button>
      <button className="" onClick={() => setInput(input + "+")}>
        +
      </button>
      <br />
      <button className="" onClick={() => setInput(input + "4")}>
        4
      </button>
      <button className="" onClick={() => setInput(input + "5")}>
        5
      </button>
      <button className="" onClick={() => setInput(input + "6")}>
        6
      </button>
      <button className="" onClick={() => setInput(input + "-")}>
        -
      </button>
      <br />
      <button className="" onClick={() => setInput(input + "1")}>
        1
      </button>
      <button className="" onClick={() => setInput(input + "2")}>
        2
      </button>
      <button className="" onClick={() => setInput(input + "3")}>
        3
      </button>
      <button className="" onClick={() => setInput(input + "*")}>
        x
      </button>
      <br />
      <button className="" onClick={() => clearn()}>
        C
      </button>
      <button className="" onClick={() => setInput(input + "0")}>
        0
      </button>
      <button className="" onClick={() => setInput(input + ".")}>
        .
      </button>
      <button className="" onClick={() => setInput(input + "/")}>
        /
      </button>
      <br />
      <button
        className="ac"
        style={{ width: "145px" }}
        onClick={() => setInput("")}
      >
        AC
      </button>
      <button
        className="equal"
        style={{ width: "140px" }}
        onClick={() => result()}
      >
        =
      </button>
    </div>
  );
}

export default Calculator;
